<template>
  <div id="edit-employee">
    <h3>Edit Employee</h3>
    <form @submit.prevent="updateEmployee" class="col s12">
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="employee_id" required disabled>
          <label v-if="employee_id==null">Employee ID#</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="name" required>
          <label v-if="name==null">Name</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="dept" required>
          <label v-if="dept==null">Department</label>
        </div>
      </div>
      <div class="row">
        <div class="input-field col s12">
          <input type="text" v-model="position" required>
          <label v-if="position==null">Position</label>
        </div>
      </div>
      <button type="submit" class="btn btn-success">Submit</button>
      <router-link to="/" class="btn grey">Cancel</router-link>
    </form>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'edit-employee',
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    },
    //Executes before page loads
    //@params:to is the navigation url where it is headed it has the parameter employee_id because that was specified in the router
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id,
                vm.name = doc.data().name,
                vm.dept = doc.data().dept,
                vm.position = doc.data().position
            })
          })
        })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employee_id = doc.data().employee_id,
                this.name = doc.data().name,
                this.dept = doc.data().dept,
                this.position = doc.data().position
            })
          })
      },
      updateEmployee() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              doc.ref.update({
                employee_id: this.employee_id,
                name: this.name,
                dept: this.dept,
                position: this.position
              }).then(() => {
                this.$router.push({
                  name: 'view-employee',
                  params: {
                    employee_id: this.employee_id
                  }
                })
              })
            })
          })
      }
    }
  }

</script>

<style>


</style>



