<template>
  <div id="view-employee">
    <ul class="collection with-header">
      <li class="collection-header">
        <h4>{{name}}</h4>
      </li>
      <li class="colelction-item">Employee ID#:{{employee_id}}</li>
      <li class="colelction-item">Department: {{dept}}</li>
      <li class="colelction-item">Position: {{position}}</li>
    </ul>
    <router-link v-bind:to="{name: 'edit-employee',params:{employee_id: employee_id}}" class="btn green">Edit</router-link>
    <button class="btn red" v-on:click="deleteEmployee">Delete</button>
    <router-link to="/" class="btn grey">Back</router-link>
    <div class="fixed-action-btn" id="edit-btn">
        <router-link v-bind:to="{name: 'edit-employee',params:{employee_id: employee_id}}" class="btn-floating btn-large green">
          <i class="fa fa-pencil"></i>
        </router-link>
      </div>
  </div>
</template>

<script>
  import db from './firebaseInit'
  export default {
    name: 'view-employee',
    data() {
      return {
        employee_id: null,
        name: null,
        dept: null,
        position: null
      }
    },
    //Executes before page loads
    //@params:to is the navigation url where it is headed it has the parameter employee_id because that was specified in the router
    beforeRouteEnter(to, from, next) {
      db.collection('employees').where('employee_id', '==', to.params.employee_id).get()
        .then(querySnapshot => {
          querySnapshot.forEach(doc => {
            next(vm => {
              vm.employee_id = doc.data().employee_id,
                vm.name = doc.data().name,
                vm.dept = doc.data().dept,
                vm.position = doc.data().position
            })
          })
        })
    },
    watch: {
      '$route': 'fetchData'
    },
    methods: {
      fetchData() {
        db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
          .then(querySnapshot => {
            querySnapshot.forEach(doc => {
              this.employee_id = doc.data().employee_id,
                this.name = doc.data().name,
                this.dept = doc.data().dept,
                this.position = doc.data().position
            })
          })
      },
      deleteEmployee() {
        if (confirm('Are you sure?')) {
          db.collection('employees').where('employee_id', '==', this.$route.params.employee_id).get()
            .then(querySnapshot => {
              querySnapshot.forEach(doc => {
                doc.ref.delete()
                this.$router.push('/')
              })
            })
        }
        return;
      }
    }
  }

</script>

<style>


</style>



